import axios from "axios";
import { tansParams } from "./tool";
import errorCode from "@/utils/errorCode";
import { Notify } from 'vant';



// axios
// const service = axios.create({
//     baseURL:process.env.VUE_APP_BASE_API,    // 
//     timeout: 10000,
// });

let baseURL = "/";
if (process.env.NODE_ENV === 'production') {
    baseURL = process.env.VUE_APP_BASE_API;
}

const service = axios.create({
    baseURL,
    timeout: 10000,
})

// request
service.interceptors.request.use(
    (config) => {
        if (!config.headers.notToken) {
            config.headers.token = localStorage.getItem("token");
        }

        if(process.env.NODE_ENV === 'production'){
            config.url = config.url.replace(/^\/api/,"");
        }

        // getparams
        if (config.method === "get" && config.params) {
            console.log(config.url, "config.url");
            let url = config.url + "?" + tansParams(config.params);
            url = url.slice(0, -1);
            config.params = {};
            config.url = url;
        }

        console.log(config);
        return config;
    },
    (error) => {
        console.log(error);
        Promise.reject(error);
    }
);

// 
service.interceptors.response.use(
    (res) => {
        // 
        const code = res.data.code || 200;
        // 
        const msg = errorCode[code] || res.data.msg || errorCode["default"];
        // 
        if (
            res.request.responseType === "blob" ||
            res.request.responseType === "arraybuffer"
        ) {
            return res.data;
        }
        if (code === 401) {
            console.log("==401", msg);
            return Promise.reject("");
        } else if (code === 500) {
            console.log("==500", msg);
            if (!res.config.headers.noterrdialog) {
                Notify(msg);
            }
            return Promise.reject(new Error(msg));
        } else if (code !== 200) {
            console.log("!=200", msg);
            if (!res.config.headers.noterrdialog) {
                Notify(msg);
            }
            return Promise.reject(new Error(msg))
        } else {

            if (res.config.headers.successdialog) {
                let dialogMsg = res.config.headers.successMsg || '';
                Notify({ type: 'success', message: decodeURIComponent(dialogMsg) });
            }
            return res.data;
        }
    },
    (error) => {
        console.log("err" + error);
        let { message } = error;
        if (message == "Network Error") {
            message = "";
        } else if (message.includes("timeout")) {
            message = "";
        } else if (message.includes("Request failed with status code")) {
            message = "" + message.substr(message.length - 3) + "";
        }

        Notify({
            message: message,
            duration: 5000,
        });

        console.log(message);
        return Promise.reject(error);
    }
);

export default service;


